.gridLayout {
  :global {
    .react-grid-layout {
      position: relative;
      transition: height 200ms ease;
    }

    .react-grid-item {
      transition: all 200ms ease;
      transition-property: left, top;
    }

    .react-grid-item.cssTransforms {
      transition-property: transform;
    }

    .react-grid-item.resizing {
      z-index: 1;
      will-change: width, height;
    }

    .react-grid-item.react-draggable-dragging {
      transition: none;
      z-index: 3;
      will-change: transform;
    }

    .react-grid-item.react-grid-placeholder {
      background: red;
      opacity: 0.2;
      transition-duration: 100ms;
      z-index: 2;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      -o-user-select: none;
      user-select: none;
    }

    .react-grid-item>.react-resizable-handle {
      position: absolute;
      width: 20px;
      height: 20px;
      bottom: 0;
      right: 0;
      background: url("data:image/svg+xml;base64,");
      background-position: bottom right;
      padding: 0 3px 3px 0;
      background-repeat: no-repeat;
      background-origin: content-box;
      box-sizing: border-box;
      cursor: se-resize;
    }

    #content {
      width: 100%;
    }

    .layoutJSON {
      background: #ddd;
      border: 1px solid black;
      margin-top: 10px;
      padding: 10px;
    }

    .columns {
      -moz-columns: 120px;
      -webkit-columns: 120px;
      columns: 120px;
    }

    .react-grid-item {
      box-sizing: border-box;
    }

    .react-grid-item.resizing {
      opacity: 0.9;
    }

    .react-grid-item.static {
      background: #fff;
      border: none;

      .react-resizable-handle {
        display: none;
      }
    }

    .react-grid-item .text {
      font-size: 24px;
      text-align: center;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      height: 24px;
    }

    .react-grid-item .minMax {
      font-size: 12px;
    }

    .react-grid-item .add {
      cursor: pointer;
    }

    .react-grid-dragHandleExample {
      cursor: move;
      /* fallback if grab cursor is unsupported */
      cursor: grab;
      cursor: -moz-grab;
      cursor: -webkit-grab;
    }

    li b {
      font-size: 19px;
      line-height: 14px;
    }
  }

  :global {
    .ant-divider-horizontal.ant-divider-with-text {
      padding: 8px 0;
      margin: 0;
      font-size: 12px;
    }

    .ant-col-5 {
      display: block;
      flex: 0 0 20%;
      max-width: 20%;
    }
  }

  .divider {
    display: flex;
    align-items: center;

    .add {
      display: flex;
      align-items: center;
      color: rgba(0, 0, 0, 0.45);
      user-select: none;

      &:hover {
        color: #1890ff;
      }

      .text {
        margin-left: 5px;
      }
    }
  }

  .mmDroppablePlaceholder {
    height: 100%;
    text-align: center;
    background-color: #f0f0f0;
    border: 1px dashed #aaa;
    align-items: center;
    justify-content: center;
    color: #666;
    font-weight: lighter;
    font-size: 13px;
    user-select: none;

    p {
      margin-top: 16px;
    }
  }

  .dragging {
    background-color: #2f8fe9a8;
  }
}